.message__send{
    background-color:#fff;
    padding-bottom: 10px;
}
.message__send .nickname{
    margin-left:10px;
    width:90%;
    display: block;
    border:2px solid #ccc;
}
.message__send textarea{
    margin-left:10px;
    width:90%;
    height:50px;
    display: block;
    border:2px solid #ccc;
}
.message__send .message__button{
    display:block;
    margin:10px;
}
.mesDis_img{
    width:50px;
    height: 50px;
    display: inline-block;
    border: 1px solid #ccc;
    margin:10px 0 0 30px;
}
.mesDis_left{
    float: left;
}
.mesDis_right{
    float: left;
    margin: 10px;
    width:70%;
    height:auto;
}
.mesDis_user{
}
.mesDis_user span{
    color:#666;
    font-size: 12px;
}
.mesDis_content{
    word-break: break-all;
}
ul.messul{
    background:#fff;
    margin-top:30px;
}
li{
    display: block;
    height: auto;
    padding:10px 0;
    border-bottom: 2px solid #ccc ;
}
li:after{content: '';display: block;visibility: hidden;clear: both}