.header{
    width:100%;
    position: fixed;
    top:0;
}
div.top{
    height:40px;
    background:#666;
    text-align: center;
}
.top_left{
    float:left;
    margin:10px 0 0 10px;
}
.top_right{
    color:#fff;
    display: inline-block;
    padding: 10px 0 10px 0;
    margin-left:-10px;
}