body{
    background:#eee;
}
.home{
    background:#ddd;
}
.about{
    width: 100%;
    padding-top:20px;
    background:#fff;
}
.about_h5{
    margin:0;
    text-align: center;
}
.about_div{
    padding:10px;
    text-indent: 2em;
}
.hot_posts {
    clear: both;
    background-color: #fff;
}
.title {
    position: relative;
    height: 45px;
    border-bottom: 1px solid #90bba8;
    margin: 5px 20px;
}
.home li {
    line-height: 20px;
}
.hot_posts ul {
    margin: 0 20px 10px;
    padding: 15px 0 10px;
    list-style: none;
    overflow: hidden;
}
.hot_posts li {
    overflow: hidden;
    clear: both;
    margin-bottom: 10px;
    height: 20px;
}
p {
    margin: 0 0 10px;
}
.hot_posts p {
    float: right;
    font-size: 14px;
    color: #777;
    font-size: 13px;
    margin-left: 10px;
}
.hot_posts .muted {
    margin-left: 20px;
    color: #999;
}
.hot_posts a {
    font-size: 14px;
}
.home a {
    color: #00a67c;
    text-decoration: none;
}
.life a {
    color: #00a67c;
    text-decoration: none;
}
.technique a {
    color: #00a67c;
    text-decoration: none;
}
.muted .action {
    background-color: #fff!important;
    border: 0;
    padding: 0;
    color: #f78585!important;
    font-size: 13px;
}
.fa {
    display: inline-block;
    font-family: FontAwesome;
    font-style: normal;
    font-weight: 400;
    line-height: 1;
}
.ant-tag {
    margin-bottom: 8px;
}
.hot_posts .label {
    margin-right: 8px;
    padding: 2px 7px;
    top: -1px;
    position: relative;
    display: inline-block;
    padding: 4px 6px 3px;
    line-height: 14px;
    color: #fff;
    vertical-align: baseline;
    white-space: nowrap;
}
.excerpt {
    background-color: #fff;
    padding: 20px 15px 30px 20px;
    margin-bottom: 10px;
    position: relative;
    clear: both;
}
.excerpt:before {
    display: table;
    content: "";
    line-height: 0;
}
.focus {
    width: 100px;
    height: 68px;
    margin: 0 10px 0 0;
    float: left;
    display: inline;
    text-align: center;
    position: relative;
    overflow: hidden;
}
.excerpt header {
    margin-bottom: 5px;
    margin-right: 0;
}
.excerpt header .label {
    display: block!important;
    margin: 3px 8px 0 0;
    font-size: 13px;
    float: left;
    height: 11px;
    line-height: 13px;
    position: relative;
    top: -2px;
    padding: 2px 6px 4px;
    color: #fff;
    vertical-align: baseline;
    white-space: nowrap;
}
.excerpt header h2 {
    font-size: 16px;
    line-height: 20px;
    display: block;
    margin-left: 10px;
    font-weight: 400;
    position: relative;
    top: 1px;
}
.excerpt header h2 a {
    font-size: 14px;
    position: relative;
    top: -2px;
}
.excerpt span.note {
    font-size: 13px;
    display: block;
    height: 56px;
    padding-top: 3px;
    overflow: hidden;
    line-height: 20px;
    color: #777;
}
.excerpt .focus img {
    max-width: 100px;
    max-height: 68px;
}